/* -------------- _header.scss start ----------------*/
div#sticky-header {
  z-index: 9 !important;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
  transition: 200ms all ease-in-out;
  backdrop-filter: none;
}

div#sticky-header-sticky-wrapper.is-sticky #sticky-header {
  /* backdrop-filter: blur(5px); */
  box-shadow: inset 0 -1px 0 0 var(--wp--preset--color--black-transparent-10);
  background-color: var(--wp--preset--color--black-transparent-30) !important;
  /* transition-timing-function: var(--tw-ease, var(--default-transition-timing-function)); */
  /* transition-timing-function: ease; */
  /* transition-duration: var(--tw-duration, var(--default-transition-duration)); */
  /* transition-duration: 1000ms; */
  /* transform: scale(1, 0.9); */
  padding-top: 10px !important;
  padding-bottom: 10px !important;
}

@media only screen and (min-width: 600px) {
  div#sticky-header-sticky-wrapper.is-sticky #sticky-header {
    backdrop-filter: blur(5px);
  }
}
/* -------------- header navigation ----------------*/
.wp-block-navigation-item__content {
  padding-bottom: 3px;
  text-decoration: none;
  position: relative;
  overflow: hidden;
}

.wp-block-navigation-item__content::before {
  margin-top: 0px;
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 100%;
  height: 1px;
  background: var(--wp--preset--color--accent-light);
  transition: right 0.3s;
  text-decoration: none;
}

.wp-block-navigation-item__content:hover::before {
  right: 0;
}

.wp-block-navigation-submenu li a:hover::before {
  right: 50%;
}

.current-menu-item .wp-block-navigation-item__content:before {
  width: 100%;
}

/* -------------- header navigation  ----------------*/
/* scrool to top */
p.mc-sigma-scrool-top {
  display: none;
  z-index: 999;
}

p.mc-sigma-scrool-top:hover:before {
  background-color: var(--wp--preset--color--button-bg-hover) !important;
  border-color: var(--wp--preset--color--button-bg-hover) !important;
  color: var(--wp--preset--color--button-hover-text-color) !important;
}

.mc-sigma-scrool-top::before {
  font-size: 17px;
  padding: 11.5px 13px;
  cursor: pointer;
  background: var(--wp--preset--color--button-bg);
  color: #fff;
  content: "\f062";
  font-family: "Font Awesome 5 Free";
  font-weight: 600;
  position: fixed;
  bottom: 50px;
  right: 30px;
  border-radius: 100%;
}

@media screen and (max-width: 600px) {
  .mc-sigma-scrool-top::before {
    font-size: 20px;
    padding: 11.5px 13px;
    cursor: pointer;
    background: transparent;
    color: var(--wp--preset--color--button-hover-color);
    content: "\f062";
    font-family: "Font Awesome 5 Free";
    font-weight: 600;
    position: fixed;
    bottom: 8px;
    right: 15px;
  }
}
p.mc-sigma-scrool-top {
  position: absolute;
}

/* scrool to top */
